import React from "react";
const HomePage = () => {
  return (
    <div className="bg-gray-100 mt-10" >
      <div className="bg-blue-500 text-white px-6 py-8">
        <div className="container mx-auto grid md:grid-cols-2 gap-6 items-center">
          <div>
            <h1 className="text-2xl md:text-4xl font-bold">
              An easy way to send requests to all suppliers
            </h1>
            <p className="mt-4 text-lg">
              Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do
              eiusmod tempor incididunt.
            </p>
          </div>
          {/* Right Section - Inquiry Form */}
          <div className="bg-white p-6 rounded-lg shadow-md">
            <h2 className="text-xl font-semibold mb-4">Send quote to suppliers</h2>
            <form className="space-y-4">
              <input
                type="text"
                placeholder="What item you need?"
                className="w-full border border-gray-300 rounded-lg px-4 py-2"
              />
              <textarea
                placeholder="Type more details"
                className="w-full border border-gray-300 rounded-lg px-4 py-2 h-24"
              ></textarea>
              <div className="flex items-center space-x-4">
                <input
                  type="number"
                  placeholder="Quantity"
                  className="w-full border border-gray-300 rounded-lg px-4 py-2"
                />
                <select className="w-full border border-gray-300 rounded-lg px-4 py-2">
                  <option value="pcs">Pcs</option>
                  <option value="kg">Kg</option>
                  <option value="liters">Liters</option>
                </select>
              </div>
              <button className="w-full bg-blue-500 text-white py-2 rounded-lg hover:bg-blue-600">
                Send Inquiry
              </button>
            </form>
          </div>
        </div>
      </div>

      {/* Recommended Items */}
      <div className="container mx-auto py-10">
        <h2 className="text-2xl font-bold mb-6">Recommended items</h2>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
          {/* Example Items */}
          {[
            { title: "T-shirts with multiple colors", price: "$10.30" },
            { title: "Jeans shorts for men", price: "$12.50" },
            { title: "Brown winter coat", price: "$34.00" },
            { title: "Leather wallet", price: "$19.00" },
            { title: "Canon camera", price: "$99.99" },
            { title: "Headset for gaming", price: "$29.90" },
            { title: "Smartwatch silver color", price: "$30.00" },
            { title: "Ceramic jar", price: "$19.20" },
          ].map((item, index) => (
            <div
              key={index}
              className="bg-white p-4 rounded-lg shadow-md text-center"
            >
              <div className="h-32 bg-gray-200 mb-4 rounded"></div>
              <h3 className="text-lg font-semibold">{item.title}</h3>
              <p className="text-blue-500 font-bold mt-2">{item.price}</p>
            </div>
          ))}
        </div>
      </div>

      {/* Extra Services */}
      <div className="bg-white py-10">
        <div className="container mx-auto">
          <h2 className="text-2xl font-bold mb-6">Our extra services</h2>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
            {[
              { title: "Source from Industry Hubs", icon: "🔍" },
              { title: "Customize Your Products", icon: "🎨" },
              { title: "Fast, reliable shipping", icon: "🚚" },
              { title: "Product monitoring", icon: "🔎" },
            ].map((service, index) => (
              <div
                key={index}
                className="flex items-center bg-gray-100 p-4 rounded-lg shadow-md"
              >
                <span className="text-3xl mr-4">{service.icon}</span>
                <h3 className="text-lg font-semibold">{service.title}</h3>
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* Suppliers by Region */}
      <div className="container mx-auto py-10">
        <h2 className="text-2xl font-bold mb-6">Suppliers by region</h2>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-center">
          {[
            "United States",
            "United Kingdom",
            "Pakistan",
            "Italy",
            "Russia",
            "China",
            "Australia",
            "India",
          ].map((region, index) => (
            <div
              key={index}
              className="bg-white p-4 rounded-lg shadow-md hover:bg-gray-100"
            >
              <h3 className="text-lg font-semibold">{region}</h3>
            </div>
          ))}
        </div>
    </div>
      </div>
  );
};

export default HomePage;
